main {  
  font-family: sans-serif;  
  padding: 0;  
}

$a-color: rgb(52, 91, 196);
$b-color: rgb(36, 136, 36);
$bg-color: #000;

.well-plate--frame {    
  font-family: sans-serif;  
  box-sizing: content-box; // resizeWellPlate calc the inner frame size
  border: 2px #000 solid;
  background-color: $bg-color;
  border-radius: 1em;
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  &[data-sensor="a-sensor"] {
    border-color: $a-color;
    color: $a-color;
    .well-plate--well {
      .well-plate--well-circle { border-color: $a-color;}
      &:hover .well-plate--well-circle { background: rgba($color: $a-color, $alpha: 0.4); }
      &[data-selected="true"] .well-plate--well-circle { background: $a-color; }
    }    
  }

  &[data-sensor="b-sensor"] {
    border-color: $b-color;
    color: $b-color;
    .well-plate--well {
      .well-plate--well-circle { border-color: $b-color;}
      &:hover .well-plate--well-circle { background: rgba($color: $b-color, $alpha: 0.4); }      
      &[data-selected="true"] .well-plate--well-circle { background: $b-color; }
    }    
  }
}

.well-plate--well {
  display: inline-block;
  position: relative;
  width: 4.5em;
  height: 4.5em;
  .well-plate--well-circle {
    display: block;
    width: 90%;
    height: 90%;
    border: 2px #000 solid;
    box-sizing: border-box;
    border-radius: 50%;  
    margin: 5% 0 0 5%;
  }
}

.well-plate--row-label,
.well-plate--column-label {
  display: inline-block;
  font-weight: 300;
  text-align: center;
  pointer-events: none;
  user-select: none;
}

.well-plate--row-label {
  width: 3em;
  height: 5em;
  line-height: 5em;
  padding: 0;
  margin: 0;
  position: absolute;
  left: -3em;
  top: 0;
}

.well-plate--column-label {
  height: 3em;  
  line-height: 3em;
  width:  5em;  
}

.well-plate--row {
  height: 5em;
  position: relative;
}

// --------------- test.scss ----------------

.well-plate--info {
  height: 2em;
  line-height: 2em;
  text-align: center;
  width: 5em;
  border-radius: .3em;
  color: white;
  margin: 1em;  
  &[data-sensor="a-sensor"] { background: $a-color;}
  &[data-sensor="b-sensor"] { background: $b-color;}
}

.well-plate--box {
  position: relative;
  margin: 0 auto;
}

html, body {
  margin: 0;
  padding: 0;
  background: #222;
}

.test-app--selector {
  margin: 1em;
  .test-app--selector-option {
    background: #234;
    height: 2em;
    line-height: 2em;
    text-align: center;
    color:#789;
    margin: .2em;
    padding: 1em;
    border-radius: .4em;
    span { user-select: none; }
    
    &[data-selected="true"] { 
      background: #567;
      color: white; 
    }
  }
} 

.test-app--button-line {
  margin: 1em;
}

.test-app--circle-button {
  background: #567;
  color: white;
  text-align: center;
  border-radius: 50%;
  width: 3em;
  height: 3em;
  display: inline-block;
  line-height: 3em;
  margin: .2em;
  font-weight: 700;
  span { user-select: none; }
}